import uniStatusBar from "@/components/uni-ui/uni-status-bar/uni-status-bar.vue"

export default {
	components:{
		uniStatusBar
	},
	data() {
		return {
			focusClass:{
				username:false,
				password:false
			},
			username: "",
			password: "",
			check: "",
			// 验证规则
			rules: {
				username:[
					{
						rule:/^[a-zA-Z]\w{5,17}$/,
						msg:"以字母开头，长度在6~18之间，只能包含字母、数字和下划线"
					}
				],
				password:[
					{
						rule:/^.{5,20}$/,
						msg:"长度为3-20的所有字符" 
					}
				]
			}
		}
	},
	methods: {
		goBack() {
			uni.navigateBack({
				delta:1
			})
		},
		forget() {
			
		},
		focus(key){
			this.focusClass[key] = true
		},
		blur(key){
			this.focusClass[key] = false
		},
		// 提交表单
		submit() {
			// 验证用户名
			this.rules.username.forEach((v) => {
				if (!(v.rule).test(this.username)) {
					console.log(v.msg)
				}
			})
			// 验证密码
			this.rules.password.forEach((v) => {
				if (!(v.rule).test(this.password)) {
					console.log(v.msg)
				}
			})
		}
	}
}
